<template>
  <div class="l-game-dialog">
    <transition
      :name="transition"
    >
      <div
        class="l-game-dialog-content"
        v-show="showPopper"
        @click="contentClick"
      >
        <slot name="content"></slot>
      </div>
    </transition>
    <div v-show="showPopper" class="l-game-dialog-masker" @click="hidePop"></div>
    <slot name="user"></slot>
    <slot name="reference"></slot>
  </div>
</template>

<script>
import { addClass, on } from '../../assets/scripts/dom'

export default {
  props: {
    transition: {
      type: String,
      default: 'slide-fade'
    }
  },
  data () {
    return {
      showPopper: false
    }
  },
  methods: {
    togglePop (e) {
      this.showPopper = !this.showPopper
    },
    contentClick (e) {
      /** 阻止冒泡触发事件 */
      e.stopPropagation()
    },
    hidePop () {
      this.showPopper = false
    }
  },
  mounted () {
    // console.log(this.$slots.body[0].elm.offsetTop)
    // document.body.offsetTop
    if (this.$slots.reference && this.$slots.reference[0]) {
      const reference = this.referenceElm = this.$slots.reference[0].elm
      on(reference, 'click', this.togglePop)
      // on(document, 'click', this.hidePop)
      addClass(reference, '')
    }
  }
}
</script>

