  .l-game-dialog {
    &-content {
      // background-color: rgba(255,165,0, 0.7);
      background-image: url(../../assets/images/tree.jpeg);
      padding: 10px;
      // opacity: 0.5;
      border-radius: 10px;
      position: fixed;
      z-index: 20;
      width: 60vw;
      height: 60vw;
      top: 20vh;
      left: 20vw;
      color: #fff;
    }
    &-masker {
      position: fixed;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      z-index: 10;
      background-color: #000;
      opacity: 0.3;
    }
  }
</style>
