<template>
  <div class="member-info">
    <l-card class="member-info-base" v-if="userInfo">
      <div class="member-info-base-l">
        <img
          src="https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png"
          class="l-avatar"
        />
        <div class="member-info-base-l-some">
          <div>{{userInfo.nickName}}</div>
          <div>账号剩余芯豆： {{userInfo.points}}</div>
        </div>
      </div>
      <div class="member-info-base-l">当前等级：{{userInfo.vipLevel}}</div>
      <div class="member-info-base-l">
        <div class="member-info-base-l-label">累计获得芯豆：</div>
        <div class="member-info-base-l-value">{{userInfo.totalPoints}}</div>
      </div>
      <div
        class="member-info-base-help"
        @click="showHelp = true"
      >
        <van-icon name="question-o" size="1.2rem" />
        <span>玩法规则</span>
      </div>
    </l-card>
    <l-card class="member-info-baby" v-if="userInfo">
      <van-field
        v-model="userInfo.nickName"
        label="姓名"
        placeholder="输入姓名"
        required
      />
      <van-field
        v-model="userInfo.phoneNumber"
        label="手机号码"
        placeholder="输入手机号码"
      />
      <van-cell
        title="宝宝生日"
        :value="birthdayStr"
        value-class="cell-value-right"
        @click="showBirthday = true"
      />
      <van-popup v-model="showBirthday" position="bottom">
        <van-datetime-picker
          v-model="birthday"
          type="date"
          :min-date="minDate"
          :max-date="maxDate"
          @confirm="showBirthday = false"
          @cancel="showBirthday = false"
        />
      </van-popup>
      <van-field
        v-model="userInfo.baby"
        label="宝宝性别"
        placeholder="输入宝宝性别"
      />
      <van-field
        v-model="userInfo.consignee"
        label="收货人"
        placeholder="输入收货人"
      />
      <van-field
        v-model="userInfo.address"
        label="收货人地址"
        placeholder="输入收货人地址"
      />
    </l-card>
    <van-button class="member-info-btn" type="primary" block @click="updateUserInfo">保存修改</van-button>
    <van-popup v-model="showHelp" class="member-info-instruction">
      <div class="member-info-instruction-close">
        <van-icon name="close" size="1.5rem" @click="showHelp = false" />
      </div>
      <div class="member-info-instruction-title">玩法规则</div>
      <div class="member-info-instruction-content">
        Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aenean euismod bibendum laoreet. Proin gravida dolor sit amet lacus accumsan et viverra justo commodo. Proin sodales pulvinar tempor. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Nam fermentum, nulla luctus pharetra vulputate, felis tellus mollis orci, sed rhoncus sapien nunc eget.
      </div>
    </van-popup>
  </div>
</template>

<script>
import LCard from '../../components/l-card'
import moment from 'moment'
import { Toast } from 'vant'

export default {
  components: {
    LCard
  },
  data () {
    return {
      showBirthday: false,
      birthday: new Date(2010, 1, 1),
      minDate: new Date(1990, 1, 1),
      maxDate: new Date(2030, 1, 1),
      showHelp: false,
      userInfo: null
    }
  },
  methods: {
    async queryUserInfo () {
      const userResp = await this.axios.post('/wxWeb/api/userHandler/queryUserInfo', {})
      this.userInfo = userResp.userInfo
    },
    async updateUserInfo () {
      await this.axios.post('/wxWeb/api/userHandler/updateUserInfo', this.userInfo)
      Toast.success('保存成功')
    }
  },
  computed: {
    birthdayStr () {
      return moment(this.birthday).format('YYYY-MM-DD')
    }
  },
  mounted () {
    this.queryUserInfo()
  }
}
</script>

