import { Toast } from 'vant'

export default (code) => {
  switch (code) {
    case 2001:
      Toast('非法的电话号码')
      break
    case 2002:
      Toast('非法的验证码')
      break
    case 2003:
      Toast('短信发送失败')
      break
    case 2004:
      Toast('短信验证失败')
      break
    case 2005:// 积分不足
      break
    case 2006:// 个人信息不完整
      break
    case 2007:// 已经登录
      break
    case 2010:// 非法的cdkey
      break
    case 2011:// cdkey超时
      break
    case 2012:// cdkey已被使用
      break
    case 3000:// 商品已被抢完
      break
    default:
  }
}
