<template>
  <div class="activity-exchange" ref="wrapper">
    <img src="" />
    <img src="" />
  </div>
</template>

<script>
export default {
  data () {
    return {
      bgSourceWidth: 750,
      bgSourceHeight: 1334,
      bgWidth: 750,
      bgHeight: 1334
    }
  },
  methods: {
    onResize () {
      const wrapperDom = this.$refs.wrapper
      const clientWidth = wrapperDom.clientWidth
      const clientHeight = wrapperDom.clientHeight
      let bgCurWidth = this.bgWidth
      let bgCurHeight = this.bgHeight
      if (clientWidth / clientHeight > this.bgSourceWidth / this.bgSourceHeight) {
        bgCurHeight = clientWidth / this.bgSourceWidth * this.bgSourceHeight
        wrapperDom.style.backgroundSize = 'auto 100%'
      } else {
        bgCurWidth = clientHeight / this.bgSourceHeight * this.bgSourceWidth
        wrapperDom.style.backgroundSize = '100% auto'
      }
      this.bgWidth = bgCurWidth
      this.bgHeight = bgCurHeight
    },
    debounceResize () {
      let timeout = null
      return () => {
        if (timeout != null) {
          clearTimeout(timeout)
        }
        timeout = setTimeout(this.onResize, 1000)
      }
    }
  },
  mounted () {
    document.body.onresize = this.debounceResize()
    this.onResize()
  }
}
</script>

<style scoped lang="less">
  .activity-exchange {
    height: 100vh;
    background: url(../../assets/images/award.jpeg), linear-gradient(rgb(230, 141, 189) 56%, rgb(179 , 149, 239) 74%, rgb(179 , 149, 239) 100%);
    background-position: center;
    background-repeat: no-repeat;
  }
</style>
