export const getHost = () => {
  switch (process.env.NODE_ENV) {
    case 'development':// 开发环境
      return 'http://ua8ins.natappfree.cc'
    case 'alpha':// 测试环境
      return ''
    case 'production':// 生产环境
      return window.location.origin
    default:
      return ''
  }
}

export const getClient = () => {
  switch (process.env.NODE_ENV) {
    case 'development':// 开发环境
      return 'http://127.0.0.1:8080'
    case 'alpha':// 测试环境
      return ''
    case 'production':// 生产环境
      return window.location.origin
    default:
      return ''
  }
}
