<template>
  <div class="member-exchange">
    <l-card class="member-exchange-info">
      <div>当前可用芯豆：{{userInfo ? userInfo.points : '暂无信息'}}</div>
      <van-button size="small" type="primary">获得更多</van-button>
    </l-card>
    <div class="member-exchange-commodities">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="queryOnSaleItems"
      >
        <l-card
          :key="saleItem.itemId"
          v-for="saleItem in saleItems"
          class="member-exchange-commodities-item"
        >
          <img
            class="member-exchange-commodities-item-image"
            :src="saleItem.pic"
          />
          <div class="member-exchange-commodities-item-title">{{saleItem.name}}</div>
          <div class="member-exchange-commodities-item-assist">
            <div class="member-exchange-commodities-item-assist-left">
              <div class="member-exchange-commodities-item-assist-left-l1">
                <div>{{saleItem.pointsNeed}}芯豆</div>
                <div>15.00</div>
              </div>
              <div class="member-exchange-commodities-item-assist-left-l2">
              <van-progress :percentage="figurePercent(saleItem.stockQuantity, saleItem.stockTotal)" :show-pivot="false" />
              </div>
              <div class="member-exchange-commodities-item-assist-left-l3">仅剩 {{saleItem.stockQuantity}} 件</div>
            </div>
            <div class="member-exchange-commodities-item-assist-right">
              <van-stepper v-model="saleItem.amount" />
              <van-button
                type="primary"
                size="small"
                @click="grabSaleItem(saleItem)"
              >马上抢</van-button>
            </div>
          </div>
        </l-card>
      </van-list>
    </div>
  </div>
</template>

<script>
import LCard from '../../components/l-card'

export default {
  components: {
    LCard
  },
  data () {
    return {
      from: 0,
      rows: 5,
      saleItems: [],
      loading: false,
      finished: false,
      userInfo: null
    }
  },
  methods: {
    async queryOnSaleItems () {
      const itemResp = await this.axios.post('/wxWeb/api/exchangeShopHandler/queryOnSaleItems', {
        from: this.from,
        to: this.from + this.rows
      })
      this.loading = false
      this.from += this.rows
      const saleItems = itemResp.items
      this.saleItems = [
        ...this.saleItems,
        ...saleItems.map(saleItem => ({
          ...saleItem,
          amount: 1
        }))
      ]
      if (saleItems.length < this.rows) {
        this.finished = true
      }
    },
    figurePercent (cur, total) {
      return (total - cur) / total * 100
    },
    async grabSaleItem (saleItem) {
      try {
        const amount = saleItem.amount
        await this.exchangeItem(saleItem.itemId, amount)
        /** 刷新兑换的数据 */
        const target = this.saleItems.find(v => v.itemId === saleItem.itemId)
        target.stockQuantity -= amount
        /** 刷新可用芯豆 */
        this.queryUserInfo()
      } catch (e) {
        console.log(e)
      }
    },
    /** 获取人员 */
    async queryUserInfo () {
      const userResp = await this.axios.post('/wxWeb/api/userHandler/queryUserInfo', {})
      this.userInfo = userResp.userInfo
    },
    async exchangeItem (itemId, amount) {
      try {
        await this.axios.post('/wxWeb/api/exchangeShopHandler/exchangeItem', {
          itemId,
          amount
        })
      } catch (e) {
        switch (e.code) {
          case 2005:// 芯豆不够
            this.$dialog.alert({
              message: '小主，芯豆不够了！',
              showConfirmButton: true,
              showCancelButton: true,
              confirmButtonText: '马上获取',
              cancelButtonText: '下次再说',
              beforeClose: (action, done) => {
                if (action === 'confirm') {
                  this.$router.replace({
                    name: 'member-center'
                  })
                }
                done()
              }
            })
            break
          case 2006:
            this.$dialog.alert({
              message: '请完善收货人信息，再兑换哟！',
              showConfirmButton: true,
              showCancelButton: true,
              confirmButtonText: '立即前往',
              cancelButtonText: '下次再说',
              beforeClose: (action, done) => {
                if (action === 'confirm') {
                  this.$router.replace({
                    name: 'member-center'
                  })
                }
                done()
              }
            })
            break
          case 3000:
            this.$dialog.alert({
              message: '来晚一步，商品已被抢完',
              showConfirmButton: true,
              showCancelButton: true,
              confirmButtonText: '放弃兑换',
              cancelButtonText: '反馈建议',
              beforeClose: (action, done) => {
                done()
              }
            })
            break
          default:
        }
      }
    }
  },
  mounted () {
    this.queryUserInfo()
  }
}
</script>

