<template>
  <div class="member-msg">
    <div class="member-msg-item" v-for="(message, index) in messages" :key="index">
      <img
        src="https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png"
        class="l-avatar-circle"
      />
      <l-card class="member-msg-item-body">
        <div class="member-msg-item-body-content">
          恭喜您获得了30000芯豆，赶快去兑换奖励吧
        </div>
        <div class="member-msg-item-body-assist">2小时前</div>
      </l-card>
    </div>
  </div>
</template>

<script>
import LCard from '../../components/l-card'

export default {
  components: {
    LCard
  },
  data () {
    return {
      messages: []
    }
  },
  methods: {
    async queryMessages () {
      const msgResp = await this.axios.post('/wxWeb/api/messageHandler/getMessagePage', {
        pageNumber: 1,
        pageCount: 10
      })
      this.messages = msgResp.messages
    }
  },
  mounted () {
    this.queryMessages()
  }
}
</script>

