<template>
  <div class="member-center" ref="wrapper" :style="`background-position-y: ${positionY}px`">
    <div class="member-center-operation">
      <div class="member-center-operation-item" @click="$router.push({name: 'member-exchange'})">
        <img src="../../assets/images/msg.svg" />
      </div>
      <div class="member-center-operation-item">
        <img src="../../assets/images/msg.svg" />
      </div>
      <div class="member-center-operation-item">
        <img src="../../assets/images/msg.svg" />
      </div>
      <div class="member-center-operation-item">
        <img src="../../assets/images/msg.svg" />
      </div>
    </div>
    <div
      v-for="(task, i) in tasks"
      :key="task.taskId"
      :style="`position: absolute;left: ${taskPosition[i][0] * widthP}px; top: ${taskPosition[i][1] * heightP}px;`"
    >
      <l-game-dialog>
        <div slot="content">
          <div>{{task.desc}}</div>
          <div v-if="task.goalSort === 1">
            <div>目标：登录 {{task.goal}} 次</div>
            <div v-if="task.taskId === currentTask.taskId">已登录 {{currentTask.progress === -2 ? task.goal : currentTask.progress}} 次</div>
          </div>
          <div v-if="task.goalSort === 3">
            <div>目标：分享 {{task.goal}} 次</div>
            <div v-if="task.taskId === currentTask.taskId">已分享 {{currentTask.progress === -2 ? task.goal : currentTask.progress}} 次</div>
          </div>
          <div v-if="task.goalSort === 4">
            <div>目标：累计 {{task.goal}} 芯豆</div>
            <div v-if="task.taskId === currentTask.taskId">已有 {{currentTask.progress === -2 ? task.goal : currentTask.progress}} 芯豆</div>
          </div>
          <div v-if="task.taskId === currentTask.taskId">
            <van-button v-if="currentTask.progress === -2" @click="rewardCurrentTask">完成任务</van-button>
            <!-- <van-button v-else>做任务</van-button> -->
          </div>
        </div>
        <div
          slot="user"
          :style="`position: absolute; top: -${blockHeight * heightP / 2}px; left: ${blockWidth * widthP / 2}px`"
          v-if="currentTask.taskId === task.taskId"
        >aa</div>
        <div slot="reference">
          <img
            :src="blockUrl(task.taskId)"
            :style="`width: ${blockWidth * widthP}px; height: ${blockHeight * heightP}px`"
          />
        </div>
      </l-game-dialog>
    </div>
  </div>
</template>

<script>
import LGameDialog from '../../components/game/l-game-dialog'
import doingBlock from '../../assets/images/doing.png'
import doneBlock from '../../assets/images/done.png'

export default {
  components: {
    LGameDialog
  },
  data () {
    return {
      bgWidth: 900,
      bgHeight: 1427,
      /** 原背景图宽和高 */
      bgSourceWidth: 750,
      bgSourceHeight: 1334,
      /** 原任务块宽和高 */
      blockWidth: 113,
      blockHeight: 75,
      /** 当前图层 */
      currentCoverage: 1,
      /** 每次位移 */
      step: 0,
      /** 此时的position-y */
      positionY: 0,
      /** 最底部的position-y */
      bottomY: 0,
      /** 任务框显示 */
      taskShow: false,
      /** 宽和高比例 */
      widthP: 1,
      heightP: 1,
      /** 任务块的原位置x，y */
      taskPosition: [
        [150, 1231],
        [344, 1135],
        [523, 1050],
        [451, 853],
        [332, 761],
        [271, 619],
        [357, 491],
        [399, 357],
        [379, 252]
      ],
      currentTask: {},
      tasks: []
    }
  },
  methods: {
    blockUrl (taskId) {
      return this.currentTask.taskId >= taskId ? doneBlock : doingBlock
    },
    move () {
      this.currentCoverage++
      this.moveAction()
    },
    moveAction () {
      this.positionY += 10
      if (this.positionY < (this.bottomY + (this.step * (this.currentCoverage - 1)))) {
        requestAnimationFrame(this.moveAction)
      }
    },
    async loadAllTask () {
      const taskResp = await this.axios.post('/wxWeb/api/taskHandler/queryTaskList')
      this.currentTask = taskResp.currentTask
      this.tasks = taskResp.taskList
    },
    async rewardCurrentTask () {
      await this.axios.post('/wxWeb/api/taskHandler/rewardCurrentTask', {
        taskId: this.currentTask.taskId
      })
      this.loadAllTask()
    }
  },
  mounted () {
    // const target = this.$refs.wrapper
    // const clientWidth = target.clientWidth
    // const clientHeight = target.clientHeight
    // const bgHeight = this.bgHeight / this.bgWidth * clientWidth
    // /** 设置每次位移距离 */
    // this.step = clientHeight
    // /** 设置当前背景图的background-position-y */
    // this.positionY = -bgHeight + clientHeight
    // this.bottomY = -bgHeight + clientHeight
    // console.log(this.step, this.positionY)
    const clientWidth = document.body.clientWidth
    const clientHeight = document.body.clientHeight
    /** 宽度比例 */
    this.widthP = clientWidth / this.bgSourceWidth
    this.heightP = clientHeight / this.bgSourceHeight
    this.loadAllTask()
  }
}
</script>

