<template>
  <div class="member-register">
    <div class="member-register-base">
      <div class="member-register-base-image">
        <img
          src="http://hengan-mall.oss-cn-shenzhen.aliyuncs.com/mall/img/1545285612454.jpg"
          class="member-register-base-image-img"
        />
      </div>
      <div class="member-register-base-msg">
        <van-cell-group>
          <van-field v-model="phoneNumber" placeholder="请输入您的手机号" />
          <van-field v-model="verifyCode" clearable placeholder="请输入你的验证码">
            <van-button
              slot="button"
              size="small"
              type="default"
              @click="sendVerifyCode"
              v-if="!smsTimeout"
            >获取验证码</van-button>
            <van-button
              slot="button"
              size="small"
              type="default"
              disabled
              v-else
            >重新获取({{smsTimeout}}s)</van-button>
          </van-field>
        </van-cell-group>
      </div>
      <div class="member-register-base-operation">
        <van-button slot="button" type="primary" block @click="register">注册</van-button>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from 'vant'

export default {
  data () {
    return {
      phoneNumber: '',
      verifyCode: '',
      smsTimeout: 0
    }
  },
  methods: {
    /** 发送验证码 */
    async sendVerifyCode () {
      // 可以发送验证码
      if (!this.smsTimeout) {
        if (/^1[34578]\d{9}$/.test(this.phoneNumber)) {
          try {
            await this.axios.post('/wxWeb/api/smsHandler/getSmsCode', {
              phoneNumber: this.phoneNumber
            })
            this.smsTimeout = 60
            const time = setInterval(() => {
              if (!this.smsTimeout) {
                clearInterval(time)
                return
              }
              --this.smsTimeout
            }, 1000)
            Toast('发送验证码成功')
          } catch (e) {
          }
        } else {
          Toast('请输入正确的手机号码')
        }
      } else {
        Toast('不能重复发送验证码')
      }
    },
    /** 注册 */
    async register () {
      try {
        await this.axios.post('/wxWeb/api/smsHandler/registerPhone', {
          phoneNumber: this.phoneNumber,
          verifyCode: this.verifyCode
        })
        this.$router.replace({
          name: 'member-center'
        })
      } catch (e) {
      }
    }
  }
}
</script>

