import Vue from 'vue'
import Router from 'vue-router'
import { getClient } from './assets/scripts/env'
// import { varifyAuthorize } from './router/varify'
/** login */
import WxLogin from './views/authorize/wx-login'
/** member */
import MemberCenter from './views/member-center/member-center'
import MemberInfo from './views/member-center/member-info'
import MemberMsg from './views/member-center/member-msg'
import MemberExchange from './views/member-center/member-exchange'
import MemberRegister from './views/member-center/member-register'
/** activity */
import ActivityEntrance from './views/activity/activity-entrance'
import ActivityExchange from './views/activity/activity-exchange'
/** wx */
import WxAttention from './views/wx/wx-attention'
/** error */
import Error from './views/error/error'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [{
    name: 'wx-login',
    path: '/wx-login',
    component: WxLogin,
    meta: {
      title: '微信登录'
    }
  }, {
    name: 'member-register',
    path: '/member-register',
    component: MemberRegister,
    meta: {
      title: '会员注册'
    }
  }, {
    name: 'member-center',
    path: '/member-center',
    component: MemberCenter,
    meta: {
      title: '会员中心'
    }
  }, {
    name: 'member-info',
    path: '/member-info',
    component: MemberInfo,
    meta: {
      title: '个人信息'
    }
  }, {
    name: 'member-msg',
    path: '/member-msg',
    component: MemberMsg,
    meta: {
      title: '消息中心'
    }
  }, {
    name: 'member-exchange',
    path: '/member-exchange',
    component: MemberExchange,
    meta: {
      title: '兑换商店'
    }
  }, {
    name: 'activity-entrance',
    path: '/activity-entrance',
    component: ActivityEntrance,
    meta: {
      title: '恒安小轻芯专属福利活动'
    }
  }, {
    name: 'activity-exchange',
    path: '/activity-exchange',
    component: ActivityExchange,
    meta: {
      title: '一物一码'
    }
  }, {
    name: 'wx-attention',
    path: '/wx-attention',
    component: WxAttention,
    meta: {
      title: '关注公众号'
    }
  }, {
    name: 'error',
    path: '/error',
    component: Error,
    meta: {
      title: '错误页'
    }
  }]
})
/** 设置不需要授权的路由名称 */
const needAuthorizeSet = new Set(['member-center'])
/** 设置需要关注公众号的路由名称 */
const needSubscribeSet = new Set(['activity-entrance'])
const needRegisterSet = new Set(['member-center'])
router.beforeEach(async (to, from, next) => {
  /** 修改页面标题 */
  window.document.title = to.meta.title
  try {
    const needAuthorize = needAuthorizeSet.has(to.name)
    const needSubscribe = needSubscribeSet.has(to.name)
    const needRegister = needRegisterSet.has(to.name)
    /** 判断是否需要授权 */
    if (needAuthorize) {
      /** 授权，如果授权则直接进入 */
      const result = await Vue.axios.post('/wxWeb/wxGetAuthorizeUrl', {
        targetUrl: `${getClient()}${to.path}`
      }, {
        withCredentials: false
      })
      if (result.code === 0) {
        // 如果未授权，则跳转到服务器返回的url
        location.href = result.url
        return
      }
      /** 校验是否需要关注或注册 */
      if (needSubscribe || needRegister) {
        const userResp = await Vue.axios.get('/wxWeb/api/queryUserStatus')
        // const userResp = {
        //   subscribe: 1,
        //   register: 1
        // }
        if (needSubscribe && !userResp.subscribe) {
          // 需要关注并且尚未关注公众号，则进入关注页面
          router.replace({
            name: 'wx-attention'
          })
          return
        }
        if (needRegister && !userResp.register) {
          // 需要注册并且尚未注册，则进入注册页面
          router.replace({
            name: 'member-register'
          })
          return
        }
      }
      if (result.code === 2007) {
        // 如果授权，直接跳到目标地址
        // router.replace({
        //   name: to.name
        // })
      }
    }
  } catch (e) {
    router.replace({
      name: 'error'
    })
    return
  }
  next()
})

export default router
