<template>
  <div class="wx-login">请稍候...</div>
</template>

<script>
export default {
  data () {
    return {}
  },
  methods: {
  },
  async mounted () {
    /** 请求后端 */
    try {
      const result = await this.axios.post('/wxWeb/wxGetAuthorizeUrl', {
        targetUrl: 'http://127.0.0.1:8080/member-info'
      }, {
        withCredentials: false
      })
      console.log(result)
      location.href = result.url
    } catch (e) {
      console.log(e)
    }
  }
}
</script>

<style scoped lang="less">
  .wx-login {}
</style>
